<template>
  <div class="dashboard">
    <!-- 统计卡片 -->
    <el-row :gutter="20" class="stats-row">
      <el-col :xs="24" :sm="12" :md="6" v-for="stat in stats" :key="stat.title">
        <el-card class="stat-card" shadow="hover">
          <div class="stat-content">
            <div class="stat-icon" :style="{ backgroundColor: stat.color }">
              <el-icon :size="24">
                <component :is="stat.icon" />
              </el-icon>
            </div>
            <div class="stat-info">
              <div class="stat-value">{{ stat.value }}</div>
              <div class="stat-title">{{ stat.title }}</div>
            </div>
          </div>
          <div class="stat-trend" :class="stat.trend > 0 ? 'positive' : 'negative'">
            <el-icon>
              <ArrowUp v-if="stat.trend > 0" />
              <ArrowDown v-else />
            </el-icon>
            {{ Math.abs(stat.trend) }}%
          </div>
        </el-card>
      </el-col>
    </el-row>

    <!-- 图表区域 -->
    <el-row :gutter="20" class="charts-row">
      <el-col :xs="24" :lg="16">
        <el-card title="记录趋势" class="chart-card">
          <div class="chart-container">
            <div ref="recordTrendChart" class="chart"></div>
          </div>
        </el-card>
      </el-col>
      
      <el-col :xs="24" :lg="8">
        <el-card title="记录类型分布" class="chart-card">
          <div class="chart-container">
            <div ref="recordTypeChart" class="chart"></div>
          </div>
        </el-card>
      </el-col>
    </el-row>

    <!-- 最近活动 -->
    <el-row :gutter="20" class="activity-row">
      <el-col :xs="24" :lg="12">
        <el-card title="最近记录" class="activity-card">
          <el-timeline>
            <el-timeline-item
              v-for="record in recentRecords"
              :key="record.id"
              :timestamp="formatTime(record.createdAt)"
              placement="top"
            >
              <el-card class="timeline-card" shadow="never">
                <div class="timeline-content">
                  <h4>{{ record.title }}</h4>
                  <p>{{ record.type }}</p>
                  <el-tag size="small" :type="getRecordStatusType(record.status)">
                    {{ getRecordStatusText(record.status) }}
                  </el-tag>
                </div>
              </el-card>
            </el-timeline-item>
          </el-timeline>
        </el-card>
      </el-col>
      
      <el-col :xs="24" :lg="12">
        <el-card title="系统公告" class="activity-card">
          <div v-if="announcements.length === 0" class="empty-state">
            <el-empty description="暂无公告" />
          </div>
          <div v-else>
            <div
              v-for="announcement in announcements"
              :key="announcement.id"
              class="announcement-item"
            >
              <div class="announcement-header">
                <h4>{{ announcement.title }}</h4>
                <el-tag :type="getAnnouncementType(announcement.type)" size="small">
                  {{ announcement.type }}
                </el-tag>
              </div>
              <p class="announcement-content">{{ announcement.content }}</p>
              <div class="announcement-time">
                {{ formatTime(announcement.createdAt) }}
              </div>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>

    <!-- 快捷操作 -->
    <el-card title="快捷操作" class="quick-actions">
      <el-row :gutter="16">
        <el-col :xs="12" :sm="8" :md="6" v-for="action in quickActions" :key="action.name">
          <div class="action-item" @click="handleQuickAction(action.action)">
            <el-icon :size="32" :color="action.color">
              <component :is="action.icon" />
            </el-icon>
            <span>{{ action.name }}</span>
          </div>
        </el-col>
      </el-row>
    </el-card>
  </div>
</template>

<script setup lang="ts">
import { ref, onMounted, nextTick } from 'vue'
import { useRouter } from 'vue-router'
import { ElMessage } from 'element-plus'
import { http } from '@/utils/request'
import { API_ENDPOINTS } from '@/config/api'
import type { Record, Announcement } from '@/types'
import dayjs from 'dayjs'

const router = useRouter()

// 响应式数据
const stats = ref([
  {
    title: '总记录数',
    value: 0,
    icon: 'Document',
    color: '#409eff',
    trend: 12.5
  },
  {
    title: '今日新增',
    value: 0,
    icon: 'Plus',
    color: '#67c23a',
    trend: 8.2
  },
  {
    title: '文件数量',
    value: 0,
    icon: 'Folder',
    color: '#e6a23c',
    trend: -2.1
  },
  {
    title: '用户数量',
    value: 0,
    icon: 'User',
    color: '#f56c6c',
    trend: 5.7
  }
])

const recentRecords = ref<Record[]>([])
const announcements = ref<Announcement[]>([])

// 图表引用
const recordTrendChart = ref<HTMLElement>()
const recordTypeChart = ref<HTMLElement>()

// 快捷操作
const quickActions = [
  {
    name: '新建记录',
    icon: 'Plus',
    color: '#409eff',
    action: 'create-record'
  },
  {
    name: '文件上传',
    icon: 'Upload',
    color: '#67c23a',
    action: 'upload-file'
  },
  {
    name: '数据导出',
    icon: 'Download',
    color: '#e6a23c',
    action: 'export-data'
  },
  {
    name: '系统设置',
    icon: 'Setting',
    color: '#909399',
    action: 'system-settings'
  }
]

// 方法
const formatTime = (time: string) => {
  return dayjs(time).format('YYYY-MM-DD HH:mm')
}

const getRecordStatusType = (status: string) => {
  const statusMap: Record<string, string> = {
    draft: 'info',
    published: 'success',
    archived: 'warning'
  }
  return statusMap[status] || 'info'
}

const getRecordStatusText = (status: string) => {
  const statusMap: Record<string, string> = {
    draft: '草稿',
    published: '已发布',
    archived: '已归档'
  }
  return statusMap[status] || status
}

const getAnnouncementType = (type: string) => {
  const typeMap: Record<string, string> = {
    info: 'primary',
    warning: 'warning',
    error: 'danger',
    success: 'success'
  }
  return typeMap[type] || 'primary'
}

const handleQuickAction = (action: string) => {
  switch (action) {
    case 'create-record':
      router.push('/records/create')
      break
    case 'upload-file':
      router.push('/files')
      break
    case 'export-data':
      router.push('/export')
      break
    case 'system-settings':
      router.push('/system')
      break
    default:
      ElMessage.info('功能开发中...')
  }
}

// 加载统计数据
const loadStats = async () => {
  try {
    // 这里应该调用实际的API获取统计数据
    // const statsData = await http.get('/dashboard/stats')
    
    // 模拟数据
    stats.value[0].value = 1234
    stats.value[1].value = 56
    stats.value[2].value = 789
    stats.value[3].value = 23
  } catch (error) {
    console.error('Failed to load stats:', error)
  }
}

// 加载最近记录
const loadRecentRecords = async () => {
  try {
    const response = await http.get<{ items: Record[] }>(
      `${API_ENDPOINTS.RECORDS.LIST}?page=1&pageSize=5&sort=createdAt:desc`
    )
    recentRecords.value = response.items
  } catch (error) {
    console.error('Failed to load recent records:', error)
  }
}

// 加载公告
const loadAnnouncements = async () => {
  try {
    const response = await http.get<Announcement[]>(API_ENDPOINTS.SYSTEM.ANNOUNCEMENTS)
    announcements.value = response.slice(0, 5) // 只显示前5条
  } catch (error) {
    console.error('Failed to load announcements:', error)
  }
}

// 初始化图表
const initCharts = async () => {
  await nextTick()
  
  // 这里可以集成图表库如 ECharts
  // 由于没有安装图表库，这里只是占位
  if (recordTrendChart.value) {
    recordTrendChart.value.innerHTML = '<div style="height: 300px; display: flex; align-items: center; justify-content: center; color: #909399;">图表功能需要集成 ECharts</div>'
  }
  
  if (recordTypeChart.value) {
    recordTypeChart.value.innerHTML = '<div style="height: 300px; display: flex; align-items: center; justify-content: center; color: #909399;">图表功能需要集成 ECharts</div>'
  }
}

// 生命周期
onMounted(async () => {
  await Promise.all([
    loadStats(),
    loadRecentRecords(),
    loadAnnouncements(),
    initCharts()
  ])
})
</script>

<style scoped>
.dashboard {
  padding: 0;
}

.stats-row {
  margin-bottom: 20px;
}

.stat-card {
  height: 120px;
}

.stat-content {
  display: flex;
  align-items: center;
  gap: 16px;
}

.stat-icon {
  width: 60px;
  height: 60px;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}

.stat-info {
  flex: 1;
}

.stat-value {
  font-size: 28px;
  font-weight: bold;
  color: #303133;
  line-height: 1;
}

.stat-title {
  font-size: 14px;
  color: #909399;
  margin-top: 4px;
}

.stat-trend {
  position: absolute;
  top: 16px;
  right: 16px;
  font-size: 12px;
  display: flex;
  align-items: center;
  gap: 2px;
}

.stat-trend.positive {
  color: #67c23a;
}

.stat-trend.negative {
  color: #f56c6c;
}

.charts-row {
  margin-bottom: 20px;
}

.chart-card {
  height: 400px;
}

.chart-container {
  height: 320px;
}

.chart {
  width: 100%;
  height: 100%;
}

.activity-row {
  margin-bottom: 20px;
}

.activity-card {
  height: 400px;
  overflow-y: auto;
}

.timeline-card {
  border: 1px solid #ebeef5;
  margin-bottom: 8px;
}

.timeline-content h4 {
  margin: 0 0 8px 0;
  font-size: 14px;
  color: #303133;
}

.timeline-content p {
  margin: 0 0 8px 0;
  font-size: 12px;
  color: #909399;
}

.announcement-item {
  padding: 16px 0;
  border-bottom: 1px solid #ebeef5;
}

.announcement-item:last-child {
  border-bottom: none;
}

.announcement-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 8px;
}

.announcement-header h4 {
  margin: 0;
  font-size: 14px;
  color: #303133;
}

.announcement-content {
  margin: 0 0 8px 0;
  font-size: 13px;
  color: #606266;
  line-height: 1.5;
}

.announcement-time {
  font-size: 12px;
  color: #909399;
}

.quick-actions {
  margin-bottom: 20px;
}

.action-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  padding: 20px;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s;
  border: 1px solid #ebeef5;
}

.action-item:hover {
  background-color: #f5f7fa;
  border-color: #409eff;
  transform: translateY(-2px);
}

.action-item span {
  font-size: 14px;
  color: #606266;
}

.empty-state {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 200px;
}

/* 响应式设计 */
@media (max-width: 768px) {
  .stat-card {
    height: auto;
    padding: 16px;
  }
  
  .stat-content {
    flex-direction: column;
    text-align: center;
  }
  
  .stat-icon {
    width: 48px;
    height: 48px;
  }
  
  .stat-value {
    font-size: 24px;
  }
  
  .chart-card,
  .activity-card {
    height: auto;
    min-height: 300px;
  }
  
  .action-item {
    padding: 16px 8px;
  }
}
</style>